import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { useDispatch, useSelector } from 'react-redux';

export default function AlertDialog() {
  const deleteListModal = useSelector(state => state.deleteListModal);
  const dispatch = useDispatch();

  function handleClose() {
    dispatch({ type: 'CLOSE_DELETE_LIST_MODAL' });
  }

  function handleDelete() {
    dispatch({ type: 'CLOSE_DELETE_LIST_MODAL' });
    dispatch({ type: 'REMOVE_LIST', value: deleteListModal });
    dispatch({ type: 'REMOVE_ALL_BY_ID', value: deleteListModal });
  }

  return (
    <div>
      <Dialog
        open={deleteListModal}
        onClose={() => handleClose()}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Удалить этот список?</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Вы действительно хотите удалить этот список?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose()} color="primary">
            Отмена
          </Button>
          <Button onClick={() => handleDelete(deleteListModal)} color="secondary" autoFocus>
            Удалить
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
