import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import { Container } from '@material-ui/core';

import Appbar from './components/Appbar';
import Drawer from './components/Drawer';
import DeleteTaskModal from './components/DeleteTaskModal';
import AddTaskModal from './components/AddTaskModal';
import AddListModal from './components/AddListModal';
import DeleteListModal from './components/DeleteListModal';

import Main from './views/Main';

const App = () => (
  <Router>
    <Route path="/react-todo/:id?" component={Appbar} />
    <Route path="/react-todo/:id?" component={Drawer} />
    <DeleteTaskModal />
    <DeleteListModal />
    <AddTaskModal />
    <AddListModal />
    <Container fixed style={{ position: 'relative' }}>
      <Route exact path="/react-todo/:id?" render={props => <Main key={props.match.params.id} {...props} />} />
    </Container>
  </Router>
);

export default App;
