import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import RemoveIcon from '@material-ui/icons/Remove';
import { useDispatch, useSelector } from 'react-redux';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function CheckboxList({ match }) {
  const dispatch = useDispatch();

  const { id } = match.params;

  const tasks = useSelector(state => state.tasks);
  const search = useSelector(state => state.search);

  const classes = useStyles();

  const handleToggle = (taskId) => {
    dispatch({ type: 'TOGGLE_TASK', value: taskId });
  };

  const handleRemove = (taskId) => {
    dispatch({ type: 'OPEN_DELETE_TASK_MODAL', value: taskId });
  };

  const tasksFilter = (task) => {
    const re = new RegExp(search, 'i');
    return re.test(task.name);
  };

  return (
    <List className={classes.root}>
      {tasks.filter(task => task.listId === id).filter(tasksFilter).map(task => (
        <ListItem
          key={task.id}
          role={undefined}
          dense
          button
          onClick={() => handleToggle(task.id)}
        >
          <ListItemIcon>
            <Checkbox
              edge="start"
              checked={task.checked}
              tabIndex={-1}
              disableRipple
              inputProps={{ 'aria-labelledby': task.checked }}
            />
          </ListItemIcon>
          <ListItemText id={task.checked} primary={task.name} />
          <ListItemSecondaryAction>
            <IconButton onClick={() => handleRemove(task.id)} edge="end" aria-label="Comments">
              <RemoveIcon />
            </IconButton>
          </ListItemSecondaryAction>
        </ListItem>
      ))}
    </List>
  );
}
