import React, { useRef, useState } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { useDispatch, useSelector } from 'react-redux';

export default function FormDialog() {
  const addTaskModal = useSelector(state => state.addTaskModal);
  const dispatch = useDispatch();

  const field = useRef('');

  const useInput = () => {
    const [value, setValue] = useState(null);
    const onChange = e => setValue(e.target.value);
    return { value, onChange };
  };
  const taskInput = useInput();

  function handleClose() {
    dispatch({ type: 'CLOSE_ADD_TASK_MODAL' });
  }

  function handleAdd(e) {
    e.preventDefault();
    dispatch({ type: 'CLOSE_ADD_TASK_MODAL' });
    dispatch({
      type: 'ADD_TASK',
      value: {
        listId: addTaskModal,
        id: `${Math.random() * 1000000 & 0xfffff}`,
        name: taskInput.value,
        checked: false,
      },
    });
    taskInput.onChange({ target: { value: '' } });
  }

  return (
    <div>
      <Dialog open={addTaskModal} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Добавление задачи</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Введите имя задачи
          </DialogContentText>
          <form onSubmit={handleAdd}>
            <TextField
              ref={field}
              {...taskInput}
              autoFocus
              margin="dense"
              label="Имя задачи..."
              type="text"
              fullWidth
            />
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Отмена
          </Button>
          <Button onClick={handleAdd} color="primary">
            Добавить
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
