import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { useDispatch, useSelector } from 'react-redux';

export default function AlertDialog() {
  const deleteTaskModal = useSelector(state => state.deleteTaskModal);
  const dispatch = useDispatch();

  function handleClose() {
    dispatch({ type: 'CLOSE_DELETE_TASK_MODAL' });
  }

  function handleDelete() {
    dispatch({ type: 'CLOSE_DELETE_TASK_MODAL' });
    dispatch({ type: 'REMOVE_TASK', value: deleteTaskModal });
  }

  return (
    <div>
      <Dialog
        open={deleteTaskModal}
        onClose={() => handleClose()}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">Удалить эту задача?</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Вы действительно хотите удалить эту задачу?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose()} color="primary">
            Отмена
          </Button>
          <Button onClick={() => handleDelete(deleteTaskModal)} color="secondary" autoFocus>
            Удалить
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
