import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import { useSelector, useDispatch } from 'react-redux';
import IconButton from '@material-ui/core/IconButton';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import RemoveIcon from '@material-ui/icons/Remove';

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});

export default function SwipeableTemporaryDrawer({ history, match }) {
  const { id } = match.params;

  const drawer = useSelector(state => state.drawer);
  const lists = useSelector(state => state.lists);
  const dispatch = useDispatch();

  useEffect(() => {
    history.push(`/react-todo/${lists[lists.length - 1].id}`);
  }, [lists]);

  const classes = useStyles();

  const toggleDrawer = (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    dispatch({ type: 'TOGGLE_DRAWER' });
  };

  const handleRemove = (listId) => {
    dispatch({ type: 'OPEN_DELETE_LIST_MODAL', value: listId });
  };

  const sideList = () => (
    <div
      className={classes.list}
      role="presentation"
      onKeyDown={toggleDrawer}
      onClick={toggleDrawer}
    >
      <List>
        {lists.map(list => (
          <ListItem selected={list.id === id} onClick={() => history.push(`/react-todo/${list.id}`)} button key={list.id}>
            <ListItemText primary={list.name} />
            {lists.length > 1 && (
            <ListItemSecondaryAction>
              <IconButton onClick={() => handleRemove(list.id)} edge="end" aria-label="Comments">
                <RemoveIcon />
              </IconButton>
            </ListItemSecondaryAction>
            )}
          </ListItem>
        ))}
      </List>
    </div>
  );

  return (
    <div>
      <SwipeableDrawer
        open={drawer}
        onClose={toggleDrawer}
        onOpen={toggleDrawer}
      >
        {sideList('left')}
      </SwipeableDrawer>
    </div>
  );
}
