import { combineReducers } from 'redux';

const drawer = (state = false, { type }) => {
  switch (type) {
    case 'TOGGLE_DRAWER':
      return !state;
    default:
      return state;
  }
};

const initialList = [
  {
    id: `${Math.random() * 1000000 & 0xfffff}`,
    name: 'Мой список',
  },
];

const lists = (state = initialList, { type, value }) => {
  switch (type) {
    case 'ADD_LIST':
      return [...state, value];
    case 'REMOVE_LIST':
      return state.filter(list => list.id !== value);
    default:
      return state;
  }
};

const search = (state = '', { type, value }) => {
  switch (type) {
    case 'SET_SEARCH':
      return value;
    default:
      return state;
  }
};


const tasks = (state = [], { type, value }) => {
  switch (type) {
    case 'ADD_TASK':
      return [...state, value];
    case 'REMOVE_ALL_BY_ID':
      return state.filter(task => task.listId !== value);
    case 'REMOVE_TASK':
      return state.filter(task => task.id !== value);
    case 'TOGGLE_TASK':
      return state.map((task) => {
        if (task.id === value) {
          return Object.assign({}, task, { checked: !task.checked });
        }
        return task;
      });
    default:
      return state;
  }
};

const deleteTaskModal = (state = false, { type, value }) => {
  switch (type) {
    case 'OPEN_DELETE_TASK_MODAL':
      return value;
    case 'CLOSE_DELETE_TASK_MODAL':
      return false;
    default:
      return state;
  }
};

const deleteListModal = (state = false, { type, value }) => {
  switch (type) {
    case 'OPEN_DELETE_LIST_MODAL':
      return value;
    case 'CLOSE_DELETE_LIST_MODAL':
      return false;
    default:
      return state;
  }
};

const addTaskModal = (state = false, { type, value }) => {
  switch (type) {
    case 'OPEN_ADD_TASK_MODAL':
      return value;
    case 'CLOSE_ADD_TASK_MODAL':
      return false;
    default:
      return state;
  }
};

const addListModal = (state = false, { type }) => {
  switch (type) {
    case 'OPEN_ADD_LIST_MODAL':
      return true;
    case 'CLOSE_ADD_LIST_MODAL':
      return false;
    default:
      return state;
  }
};

export default combineReducers({
  drawer,
  lists,
  tasks,
  deleteTaskModal,
  addTaskModal,
  addListModal,
  deleteListModal,
  search,
});
